/* Help accordion */
.help-card {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  background: #ffffff;
  overflow: hidden;
}

.help-summary {
  cursor: pointer;
  padding: 12px 14px;
  font-weight: 700;
  list-style: none;
  background: #f9fafb;
}

.help-card[open] .help-summary {
  border-bottom: 1px solid #e5e7eb;
}

.help-body {
  padding: 14px;
}

.help-section + .help-section {
  margin-top: 16px;
}

.help-section h3 {
  font-size: 1.05rem;
  font-weight: 700;
  margin: 0 0 6px;
}

.help-pre {
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  padding: 10px;
  white-space: pre-wrap;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.92rem;
  line-height: 1.35;
  margin: 8px 0;
}

.help-actions {
  margin: 6px 0 2px;
}

.btn {
  border: 1px solid #d1d5db;
  background: #ffffff;
  padding: 6px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.btn:hover {
  background: #f9fafb;
}

.help-note {
  color: #6b7280;
  font-size: 0.9rem;
  margin-top: 4px;
}

.help-list {
  list-style: disc;
  padding-left: 1.25rem;
  margin: 0;
}

.help-list li + li {
  margin-top: 4px;
}

/* purple underline for hard words */
.editor-content span[data-hard-word="1"] {
  text-decoration-line: underline;
  text-decoration-style: dotted;
  text-decoration-color: #7c3aed; /* purple */
  text-decoration-thickness: 2px;
}

/* sample for legend */
.legend-underline-purple {
  text-decoration: underline;
  text-decoration-style: dotted;
  text-decoration-color: #7c3aed;
  text-underline-offset: 2px;
  font-weight: 700;
}

